"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { toast } from 'sonner'

export function TestManagement() {
  const [searchTerm, setSearchTerm] = useState('')
  const [filterStatus, setFilterStatus] = useState('all')
  const [selectedTests, setSelectedTests] = useState<string[]>([])

  const mockTests = [
    {
      id: '1',
      name: 'User Login Flow',
      type: 'E2E',
      status: 'passing',
      lastRun: '2 minutes ago',
      duration: '2.3s',
      success: true,
      healingCount: 0
    },
    {
      id: '2',
      name: 'Payment Processing',
      type: 'API',
      status: 'passing',
      lastRun: '5 minutes ago',
      duration: '1.8s',
      success: true,
      healingCount: 2
    },
    {
      id: '3',
      name: 'Product Search',
      type: 'UI',
      status: 'failed',
      lastRun: '10 minutes ago',
      duration: '4.2s',
      success: false,
      healingCount: 1
    },
    {
      id: '4',
      name: 'User Registration',
      type: 'E2E',
      status: 'healing',
      lastRun: '15 minutes ago',
      duration: '3.1s',
      success: false,
      healingCount: 0
    },
    {
      id: '5',
      name: 'Cart Operations',
      type: 'UI',
      status: 'passing',
      lastRun: '20 minutes ago',
      duration: '2.7s',
      success: true,
      healingCount: 3
    },
    {
      id: '6',
      name: 'API Authentication',
      type: 'API',
      status: 'passing',
      lastRun: '25 minutes ago',
      duration: '0.9s',
      success: true,
      healingCount: 0
    }
  ]

  const filteredTests = mockTests.filter(test => {
    const matchesSearch = test.name.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesFilter = filterStatus === 'all' || test.status === filterStatus
    return matchesSearch && matchesFilter
  })

  const handleRunTest = (testId: string) => {
    toast.success(`Running test: ${mockTests.find(t => t.id === testId)?.name}`)
  }

  const handleRunSelected = () => {
    if (selectedTests.length === 0) {
      toast.error('Please select tests to run')
      return
    }
    toast.success(`Running ${selectedTests.length} selected tests`)
  }

  const handleDeleteTest = (testId: string) => {
    toast.success(`Test deleted: ${mockTests.find(t => t.id === testId)?.name}`)
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'passing':
        return <Badge variant="default" className="bg-green-500">Passing</Badge>
      case 'failed':
        return <Badge variant="destructive">Failed</Badge>
      case 'healing':
        return <Badge variant="secondary" className="bg-yellow-500">Healing</Badge>
      default:
        return <Badge variant="outline">Unknown</Badge>
    }
  }

  const getTypeBadge = (type: string) => {
    const colors = {
      'E2E': 'bg-purple-500',
      'API': 'bg-blue-500',
      'UI': 'bg-green-500'
    }
    return <Badge variant="outline" className={colors[type as keyof typeof colors]}>{type}</Badge>
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold">Test Management</h2>
          <p className="text-muted-foreground">Manage, run, and monitor your automated tests</p>
        </div>
        <div className="flex gap-2">
          <Button variant="outline">Import Tests</Button>
          <Button>Create New Test</Button>
        </div>
      </div>

      <Tabs defaultValue="tests" className="space-y-4">
        <TabsList>
          <TabsTrigger value="tests">All Tests</TabsTrigger>
          <TabsTrigger value="suites">Test Suites</TabsTrigger>
          <TabsTrigger value="schedules">Schedules</TabsTrigger>
          <TabsTrigger value="history">Run History</TabsTrigger>
        </TabsList>

        <TabsContent value="tests" className="space-y-4">
          {/* Filters and Search */}
          <Card>
            <CardContent className="pt-6">
              <div className="flex gap-4 items-center">
                <div className="flex-1">
                  <Input
                    placeholder="Search tests..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                  />
                </div>
                <Select value={filterStatus} onValueChange={setFilterStatus}>
                  <SelectTrigger className="w-40">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Status</SelectItem>
                    <SelectItem value="passing">Passing</SelectItem>
                    <SelectItem value="failed">Failed</SelectItem>
                    <SelectItem value="healing">Healing</SelectItem>
                  </SelectContent>
                </Select>
                <Button 
                  onClick={handleRunSelected}
                  disabled={selectedTests.length === 0}
                >
                  Run Selected ({selectedTests.length})
                </Button>
              </div>
            </CardContent>
          </Card>

          {/* Test List */}
          <Card>
            <CardHeader>
              <CardTitle>Tests ({filteredTests.length})</CardTitle>
              <CardDescription>Manage your automated test cases</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {filteredTests.map((test) => (
                  <div key={test.id} className="flex items-center space-x-4 p-4 border rounded-lg">
                    <input
                      type="checkbox"
                      checked={selectedTests.includes(test.id)}
                      onChange={(e) => {
                        if (e.target.checked) {
                          setSelectedTests([...selectedTests, test.id])
                        } else {
                          setSelectedTests(selectedTests.filter(id => id !== test.id))
                        }
                      }}
                      className="rounded"
                    />
                    
                    <div className="flex-1">
                      <div className="flex items-center space-x-2 mb-1">
                        <h4 className="font-medium">{test.name}</h4>
                        {getTypeBadge(test.type)}
                        {getStatusBadge(test.status)}
                        {test.healingCount > 0 && (
                          <Badge variant="outline" className="text-blue-600">
                            Healed {test.healingCount}x
                          </Badge>
                        )}
                      </div>
                      <div className="flex items-center space-x-4 text-sm text-muted-foreground">
                        <span>Last run: {test.lastRun}</span>
                        <span>Duration: {test.duration}</span>
                      </div>
                    </div>

                    <div className="flex items-center space-x-2">
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => handleRunTest(test.id)}
                      >
                        Run
                      </Button>
                      <Button
                        variant="outline"
                        size="sm"
                      >
                        Edit
                      </Button>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => handleDeleteTest(test.id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="suites" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Test Suites</CardTitle>
              <CardDescription>Organize tests into logical groups</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {[
                  { name: 'Smoke Tests', tests: 12, lastRun: '1 hour ago', status: 'passing' },
                  { name: 'Regression Suite', tests: 45, lastRun: '3 hours ago', status: 'passing' },
                  { name: 'API Integration', tests: 23, lastRun: '6 hours ago', status: 'failed' },
                  { name: 'E2E Critical Path', tests: 8, lastRun: '12 hours ago', status: 'passing' }
                ].map((suite, index) => (
                  <div key={index} className="flex items-center justify-between p-4 border rounded-lg">
                    <div>
                      <h4 className="font-medium">{suite.name}</h4>
                      <p className="text-sm text-muted-foreground">
                        {suite.tests} tests  Last run: {suite.lastRun}
                      </p>
                    </div>
                    <div className="flex items-center space-x-2">
                      {getStatusBadge(suite.status)}
                      <Button variant="outline" size="sm">Run Suite</Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="schedules" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Scheduled Runs</CardTitle>
              <CardDescription>Automated test execution schedules</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {[
                  { name: 'Nightly Regression', schedule: 'Daily at 2:00 AM', nextRun: 'Tonight at 2:00 AM', active: true },
                  { name: 'Smoke Tests', schedule: 'Every 4 hours', nextRun: 'In 2 hours', active: true },
                  { name: 'Weekly Full Suite', schedule: 'Sundays at 6:00 PM', nextRun: 'Sunday at 6:00 PM', active: false },
                  { name: 'API Health Check', schedule: 'Every 30 minutes', nextRun: 'In 15 minutes', active: true }
                ].map((schedule, index) => (
                  <div key={index} className="flex items-center justify-between p-4 border rounded-lg">
                    <div>
                      <h4 className="font-medium">{schedule.name}</h4>
                      <p className="text-sm text-muted-foreground">
                        {schedule.schedule}  Next: {schedule.nextRun}
                      </p>
                    </div>
                    <div className="flex items-center space-x-2">
                      <Badge variant={schedule.active ? "default" : "secondary"}>
                        {schedule.active ? "Active" : "Paused"}
                      </Badge>
                      <Button variant="outline" size="sm">Edit</Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="history" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Execution History</CardTitle>
              <CardDescription>Recent test run results and logs</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {[
                  { 
                    id: 'run-1', 
                    suite: 'Smoke Tests', 
                    time: '5 minutes ago', 
                    duration: '2m 34s', 
                    passed: 11, 
                    failed: 1, 
                    status: 'completed' 
                  },
                  { 
                    id: 'run-2', 
                    suite: 'API Integration', 
                    time: '1 hour ago', 
                    duration: '4m 12s', 
                    passed: 20, 
                    failed: 3, 
                    status: 'completed' 
                  },
                  { 
                    id: 'run-3', 
                    suite: 'E2E Critical Path', 
                    time: '3 hours ago', 
                    duration: '8m 45s', 
                    passed: 8, 
                    failed: 0, 
                    status: 'completed' 
                  },
                  { 
                    id: 'run-4', 
                    suite: 'Regression Suite', 
                    time: '6 hours ago', 
                    duration: '15m 23s', 
                    passed: 42, 
                    failed: 3, 
                    status: 'completed' 
                  }
                ].map((run) => (
                  <div key={run.id} className="flex items-center justify-between p-4 border rounded-lg">
                    <div>
                      <h4 className="font-medium">{run.suite}</h4>
                      <p className="text-sm text-muted-foreground">
                        {run.time}  Duration: {run.duration}
                      </p>
                    </div>
                    <div className="flex items-center space-x-4">
                      <div className="text-sm">
                        <span className="text-green-600">{run.passed} passed</span>
                        {run.failed > 0 && (
                          <span className="text-red-600 ml-2">{run.failed} failed</span>
                        )}
                      </div>
                      <Button variant="outline" size="sm">View Report</Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
